import Axios from 'axios';

function plugins(Vue) {
  if (!plugins.installed) {
    const reqConfig = {
      timeout: 10000
    };
    const axios = Axios.create(reqConfig);
    let apiUrl = '';
    const NODE_EVN = process.env.NODE_ENV;
    if (NODE_EVN === 'development') {
      apiUrl = 'https://api.mtnhao.com';
    }
    if (NODE_EVN === 'production') {
      apiUrl = 'https://api.mtnhao.com';
    }
    const formatUrl = (url, data = {}) => {
      let urlStr = url;
      if (Object.keys(data).length) {
        Object.keys(data).forEach(item => {
          if (data[item]) {
            urlStr += `&${item}=${encodeURIComponent(data[item])}`;
          }
        });
      }
      return urlStr.replace(/&/, '?');
    };
    const replaceUrl = url => {
      return url.replace(/^\/api/, apiUrl);
    };
    const httpGet = (url, data, options) => {
      return axios.get(formatUrl(replaceUrl(url), data)).then(res => {
        return res.data;
      });
    };
    const httpPost = (url, data, options) => {
      return axios.post(replaceUrl(url), data, options).then(res => {
        return res.data;
      });
    };
    Vue.prototype.$httpGet = httpGet;
    Vue.prototype.$httpPost = httpPost;
  }
}
if (window !== undefined && window.Vue !== undefined) {
  window.Vue.use(plugins);
}
export default plugins;



