// import Vue from 'vue'
import Router from 'vue-router';
import HelloWorld from '@/components/HelloWorld';

const IndexPage = r =>
  require.ensure([], () => r(require('@/pages/index/index')));

const SearchPage = r =>
  require.ensure([], () => r(require('@/pages/search/search')));
// Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: IndexPage
    },
    {
      path: '/test',
      name: 'test',
      component: HelloWorld
    },
    {
      path: '/search',
      name: 'search',
      component: SearchPage
    }
  ]
});



