<template>
  <svg class="svg-icon">
    <use :xlink:href="'#icon-'+name"></use>
  </svg>
</template>
<script>
export default {
  props: {
    name: {
      type: String
    }
  }
};
</script>
<style>
.svg-icon {
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  height: 20px;
  fill: #666;
  margin-top: -4px;
}
</style>



