<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <select v-model="type" class="m-input">
      <option v-for="(item,index) in types" :key="index">{{item}}</option>
    </select>
    <input type="text" v-model="message">
    <button @click="showMessage">
      <v-icon name="email"></v-icon>显示消息</button>
    <button @click="closeAll">关闭所有</button>
  </div>
</template>

<script>
import VIcon from '@/components/icon';
export default {
  name: 'HelloWorld',
  components: {
    VIcon
  },
  data() {
    return {
      types: ['info', 'success', 'error', 'warning', 'loading'],
      type: 'info',
      message: 'This is a test.',
      msg: 'Welcome to Your Vue.js App'
    };
  },
  created() {
    this.$httpGet('/api/login', {
      email: 'hereres@163.com',
      password: '234324'
    });
  },
  methods: {
    showMessage() {
      this.$message({
        message: this.message,
        type: this.type,
        showClose: true
      });
    },
    closeAll() {
      this.$message.closeAll();
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



